html {
    background-image: linear-gradient(to bottom right, #39383D, #000000);
    background-repeat: no-repeat;
    background-attachment: fixed;
    height: 100%;
    scroll-behavior: smooth;
    width: 100%;
}

body {
    font-family: Helvetica, Arial, sans-serif;
    height: 100%;
    overflow-y: scroll;
    width: 100%;
}


.gif-app {
    justify-content: center;
    height: 100%;
}


